import React, { FC } from "react";

import { Icon } from "../Icon/Icon";

import { RightBar, StyledSidebarItem } from "./SidebarItem.styles";
import { SidebarItemProps } from "./SidebarItem.types";

const SidebarItem: FC<SidebarItemProps> = ({
	dataQA,
	isActive,
	navigationItemName,
	navigationItemIcon,
	isParent,
	isDisabled,
}) => (
	<StyledSidebarItem
		isActive={isActive}
		dataQA={dataQA}
		variant="bodyCopySmall"
		component="p"
		isParent={isParent}
		isDisabled={isDisabled}
	>
		{navigationItemIcon && <Icon
			size="17"
			name={navigationItemIcon}
			aria-hidden="true"
		/>}
		{navigationItemName}
		{isActive && (
			<RightBar
				isActive={isActive}
				data-qa={`${dataQA}-right-bar`}
			/>
		)}
	</StyledSidebarItem>
);

SidebarItem.displayName = "SidebarItem";
export default SidebarItem;
